
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Range from 'vux/src/components/range/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "fbpj",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XTextarea,
Rater,
Range,
XHeader,
loading
},
data() {
return {
value: "",
start: 5,
geval_level: 1
};
},
created() {
},
mounted: function() {
this.$nextTick(function() {

});
},
destroyed() {
window.onscroll = null;
},
methods: {
async tijiao() {
if (!this.value) {
this.$vux.toast.text("请填写您的评语");
return;
}
if (!this.flag) {
this.flag = true;
let data = {
plat: 3,
token: this.token,
account: this.account,
content: this.value,
order_id: this.$route.query.orderid,
goods_item_id: this.$route.query.itemid,
geval_star: this.start,
geval_level: this.geval_level,
sn: ""
};
const [err, res] = await api.newevaluategoods(data);
this.flag = false;
if (err) {
this.$vux.toast.text(err.msg);
return;
}
this.$vux.toast.text(res.msg);
} else {
console.log("函数防抖");
}
},
...mapActions(["update_storeInfo"])
},
computed: {
statusText() {
let text = "差评";
switch (this.start) {
case 1:
text = "差评";
this.geval_level = 3;
break;
case 2:
case 3:
text = "中评";
this.geval_level = 2;
break;
case 4:
case 5:
text = "好评";
this.geval_level = 1;
break;
}
return text;
},
...mapGetters(["user", "account", "token"])
}
};



